import Vue from 'vue'
import Router from 'vue-router'
import storeList from '@/components/storelist'
import register from '@/components/register'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
     redirect: '/storeList'
    },
    {
      path: '/storeList',
      name: 'storeList',
      component: storeList
    },
    {
      path: '/register',
      name: 'register',
      component:register
    },

  ],

})




